<template>
  <div>
    <el-container>
      <el-header>注册界面</el-header>
      <el-main>
        <el-form
          label-width="80px"
          :model="account"
          :rules="rules"
          ref="account"
        >
          <el-form-item label="姓名" prop="name">
            <el-input v-model="account.name"></el-input>
          </el-form-item>

          <el-form-item label="密码" prop="password">
            <el-input v-model="account.password" show-password></el-input>
          </el-form-item>

          <el-form-item label="确认密码" prop="confirmed_password">
            <el-input
              v-model="account.confirmed_password"
              show-password
            ></el-input>
          </el-form-item>

          <el-form-item label="电话号码" prop="phoneNumber">
            <el-input v-model="account.phoneNumber"></el-input>
          </el-form-item>

          <el-form-item label="教师身份凭证(教师必填项)" label-width="100px">
            <el-input v-model="account.teacherCertificate"></el-input>
          </el-form-item>

          <el-form-item label="身份" prop="type">
            <el-radio-group v-model="account.type">
              <el-radio label="1">教师</el-radio>
              <el-radio label="2">学生</el-radio>
            </el-radio-group>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="submitForm('account')"
              >创建账户</el-button
            >
            <el-button type="info"
              ><router-link to="/studentlogin">返回</router-link></el-button
            >
          </el-form-item>
        </el-form>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import axios from "axios";
import sha256 from "js-sha256";
export default {
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (this.account.password !== "") {
          this.$refs.account.validateField("confirmed_password");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.account.password) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      account: {
        name: "",
        password: "",
        confirmed_password: "",
        type: "",
        phoneNumber: "",
        teacherCertificate: "",
      },
      init: {},
      rules: {
        name: [
          { required: true, message: "请输入姓名", trigger: "change" },
          { min: 1, message: "姓名至少有一个字符", trigger: "blur" },
        ],
        password: [{ validator: validatePass, trigger: "blur" }],
        confirmed_password: [
          { validator: validatePass2, trigger: "blur" },
        ],
        phoneNumber: [
          { required: true, message: "请输入电话号码", trigger: "blur" },
          { pattern: /^\d{11}$/, message: '请输入11位数字的电话号码', trigger: 'blur' }
        ],
        type: [
          {
            required: true,
            message: "请选择身份",
            trigger: "change",
          },
        ],
      },
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // 首先验证此用户手机是否被注册
          axios({
            method: "get",
            url:
              "http://localhost:8080/SongGuo_Learning/account/validateIsNewAccount?phoneNumber=" +
              this.account.phoneNumber +
              "&type=" +
              this.account.type,
          }).then((resp) => {
            console.log(resp.data);
            if (resp.data.message == "VALIDATION_PASSED") {
              // 通过验证, 新用户
              console.log(this.account);

              // 创建传输数据postAccount
              let postAccount = {
                name: this.account.name,
                password: sha256(this.account.password),
                type: this.account.type,
                phoneNumber: this.account.phoneNumber,
                teacherCertificate: this.account.teacherCertificate,
              };
              // 再次发送axios请求
              this.confirmAccount(postAccount);
            } else if (resp.data.message == "VALIDATION_FAILED") {
              // 已存在用户
              this.$notify.error({
                title: "错误",
                message: "电话号码已经被使用, 无法注册",
              });
            } else {
              this.$notify.error({
                title: "错误",
                message: "发生未知错误",
              });
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
      
    },
    confirmAccount(postAcc) {
      console.log(this.account);
      axios({
        method: "post",
        url: "http://localhost:8080/SongGuo_Learning/account/createAccount",
        data: postAcc,
      }).then((resp) => {
        console.log(resp.data);
        if (resp.data.message == "SIGN_UP_SUCCESS") {
          // 注册成功
          let id = resp.data.data;
          this.$notify.success({
            title: "注册成功",
            message: "您已经成功注册!id :" + id + ", 务必记住!",
            duration: 0,
          });
        } else if (resp.data.message == "SIGN_UP_FAILED") {
          this.$notify.error({
            title: "错误",
            message: "注册失败, 可能是服务器出错",
          });
        } else if (resp.data.message == "TEACHER_VALIDATION_FAILED") {
          this.$notify.error({
            title: "错误",
            message: "教师验证出错, 请输入正确的身份验证码",
          });
        } else {
          this.$notify.error({
            title: "错误",
            message: "发生未知错误",
          });
        }
      });
    },
  },
};
</script>

<style>
.el-header,
.el-footer {
  background-color: #b3c0d1;
  color: #333;
  text-align: center;
  line-height: 60px;
}

.el-aside {
  background-color: #d3dce6;
  color: #333;
  text-align: center;
  line-height: 200px;
}

.el-main {
  background-color: #e9eef3;
  color: #333;
  text-align: center;
  line-height: 160px;
}

body > .el-container {
  margin-bottom: 40px;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
  line-height: 320px;
}
</style>