<template>
  <div>
    <el-container>
      <el-header
        >教师登陆页面<router-link to="/studentlogin">您是学生?</router-link>
      </el-header>
      <el-main>
        <el-form label-width="80px" :model="teacher" :rules="rules" ref="form">
          <el-form-item label="ID/电话" prop="certificate">
            <el-input v-model="teacher.certificate"></el-input>
          </el-form-item>

          <el-form-item label="密码" prop="password">
            <el-input v-model="teacher.password" show-password></el-input>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="submitForm('form')"
              >登陆</el-button
            >
            <el-button type="info"
              ><router-link to="/signup">注册</router-link></el-button
            >
          </el-form-item>
        </el-form>
      </el-main>
    </el-container>
  </div>
</template>
  
  <script>
import axios from "axios";
import sha256 from "js-sha256";

export default {
  data() {
    return {
      teacher: {
        certificate: "",
        password: "",
      },
      encryptedPassword: "",
      rules: {
        certificate: [
          { required: true, message: "请输入ID或电话号码", trigger: "blur" },
          {
            pattern: /^(?:\d{9}|\d{11})$/,
            message: "请输入9位或11位数字",
            trigger: "blur",
          },
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
      },
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          axios
            .get(
              "http://localhost:8080/SongGuo_Learning/account/loginValidation?certificate=" +
                this.teacher.certificate +
                "&type=1"
            )
            .then((resp) => {
              if (resp.data.message == "LOGIN_VALIDATION_SUCCESS") {
                //认证成功
                let encryptedPassword = sha256(this.teacher.password);
                let postTc = {
                  certificate: this.teacher.certificate,
                  password: encryptedPassword,
                };
                this.confirm(postTc);
              } else {
                this.$notify.error({
                  title: "登陆失败",
                  message: "请检查你的账号密码",
                });
              }
            });
        } else {
          return false;
        }
      });
    },
    confirm(postTc) {
      axios
        .get(
          "http://localhost:8080/SongGuo_Learning/account/loginConfirmation?type=1&certificate=" +
            postTc.certificate +
            "&password=" +
            postTc.password
        )
        .then((resp) => {
          if (resp.data.message == "LOGIN_CONFIRMATION_FAILED") {
            console.log("login failed");
            this.$notify.error({
              title: "登陆失败",
              message: "请检查你的账号密码",
            });
          } else {
            localStorage.setItem("token", resp.data.data);
            localStorage.setItem("id", resp.data.message);
            this.$router.push("/teacher_homepage_personal_info");
          }
        });
    },
  },
};
</script>
  
  <style>
</style>