import Vue from 'vue'
import VueRouter from 'vue-router'
import sign_up from '../views/login_signup/sign_up.vue'
import teacher_login from '../views/login_signup/teacher_login.vue'
import student_login from '../views/login_signup/student_login.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/signup',
    name: 'sign_up',
    component: sign_up
  },
  {
    path: '/teacherlogin',
    name: 'teacher_login',
    component: teacher_login
  },
  {
    path: '/studentlogin',
    name: 'student_login',
    component: student_login,
  },
  {
    path: '/',
    redirect: "/studentlogin"
  },
  {
    path:'/student_homepage_personal_info',
    name: 'student_home_page_info',
    component: () => import("../views/homepage_personal_info/student_homepage_personal_info.vue")
  },
  {
    path:'/teacher_homepage_personal_info',
    name: 'teacher_home_page_info',
    component: () => import("../views/homepage_personal_info/teacher_homepage_personal_info.vue")
  },
  {
    path: '/teacher_courses_manage',
    name: 'teacher_courses_manage',
    component: () => import("../views/course/teacher_courses_manage.vue")
  },
  {
    path: '/student_course_select',
    name: 'student_course_select',
    component: () => import("../views/course/student_course_select.vue")
  },
  {
    path: '/teacher_course_edit_page',
    name: 'course_page',
    component: () => import("../views/course/teacher_course_edit_page.vue")
  },
  {
    path: '/student_course_choose_to_learn',
    name: 'student_course_choose_to_learn',
    component: () => import("../views/course/student_course_choose_to_learn.vue"),
  },
  {
    path: '/course_learning_page',
    name: 'course_learning_page',
    component: () => import("../views/course/course_learning_page.vue")
  },
  {
    path: '/teacher_course_learning_progress',
    name: 'teacher_course_learning_progress',
    component: () => import("../views/course/teacher_course_learning_progress.vue")
  },
  {
    path:'/course_learning_progress',
    name:'course_learning_progress',
    component: () =>import("../views/course/course_learning_progress.vue")
  },
  {
    path: '/test',
    name: 'n',
    component: () => import("../views/discussions/discussionArea1.vue")
  }
]

const router = new VueRouter({
  routes
})

export default router