<template>
  <div>
    <el-container>
      <el-header
        >学生登陆页面
        <router-link to="/teacherlogin">您是教师?</router-link></el-header
      >
      <el-main>
        <el-form label-width="80px" :model="student" :rules="rules" ref="form">
          <el-form-item label="ID/手机号" prop="certificate">
            <el-input v-model="student.certificate"></el-input>
          </el-form-item>

          <el-form-item label="密码" prop="password">
            <el-input v-model="student.password" show-password></el-input>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="submitForm('form')"
              >登陆</el-button
            >
            <el-button type="info"
              ><router-link to="/signup">注册</router-link></el-button
            >
          </el-form-item>
        </el-form>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import axios from "axios";
import sha256 from "js-sha256";
export default {
  data() {
    return {
      student: {
        certificate: "",
        password: "",
      },
      encryptedPassword: "",
      rules: {
        certificate: [
          { required: true, message: "请输入ID或电话号码", trigger: "blur" },
          {
            pattern: /^(?:\d{9}|\d{11})$/,
            message: "请输入9位或11位数字",
            trigger: "blur",
          },
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
      },
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          axios
            .get(
              "http://localhost:8080/SongGuo_Learning/account/loginValidation?certificate=" +
                this.student.certificate +
                "&type=2"
            )
            .then((resp) => {
              if (resp.data.message == "LOGIN_VALIDATION_SUCCESS") {
                //认证成功
                console.log("validation success");
                let encryptedPassword = sha256(this.student.password);
                let postStu = {
                  certificate: this.student.certificate,
                  password: encryptedPassword,
                };
                this.confirm(postStu);
              } else {
                console.log("validation failed");
                this.$notify.error({
                  title: "登陆失败",
                  message: "请检查你的账号密码",
                });
              }
            });
        } else {
          return false;
        }
      });
    },
    confirm(postStu) {
      axios
        .get(
          "http://localhost:8080/SongGuo_Learning/account/loginConfirmation?type=2&certificate=" +
            postStu.certificate +
            "&password=" +
            postStu.password
        )
        .then((resp) => {
          if (resp.data.message == "LOGIN_CONFIRMATION_FAILED") {
            this.$notify.error({
              title: "登陆失败",
              message: "请检查你的账号密码",
            });
          } else {
            localStorage.setItem("token", resp.data.data);
            localStorage.setItem("id", resp.data.message);
            this.$router.push("/student_homepage_personal_info");
          } 
        });
    },
  },
};
</script>

